/*属性变量*/
.warning-warp {
  width: 100%;
  height: 100%;
  flex-direction: row ;
}
.ranking-table {
  width: 100%;
  text-align: center;
}
.ranking-table th {
  font-weight: normal;
}
.ranking-table .schedule .bar {
  width: 1.25rem;
  height: 0.16rem;
  border-radius: 0.09rem;
  display: inline-block;
}
.ranking-table .schedule .bar div {
  height: 0.16rem;
  background: #F2476B;
  border-radius: 0.09rem;
}
.ranking-table .schedule span {
  color: #fff;
}
.ranking-table .add-color {
  color: #F3Af1B;
}
.ranking-table .iconredianguanzhu {
  color: #F3Af1B;
  font-size: 0.22rem;
}
